import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import StoreOtherSevenTab from "../../StoreManager/StoreSeven/StoreOtherSevenTab";
import StoreSevenRequest from "../../StoreManager/StoreSeven/StoreSevenRequest/StoreSevenRequest";
import StoreSevenIssue from "../../StoreManager/StoreSeven/StoreSevenIssue/StoreSevenIssue";
import StoreSevenStocktake from "../../StoreManager/StoreSeven/StoreSevenStocktake/StoreSevenStocktake";
import StoreSevenConsumption from "../../StoreManager/StoreSeven/StoreSevenConsumption/StoreSevenConsumption";
import StoreSevenReturn from "../../StoreManager/StoreSeven/StoreSevenReturn/StoreSevenReturn";
import StoreSevenMaintenance from "../../StoreManager/StoreSeven/StoreSevenMaintenance/StoreSevenMaintenance";
import StoreSevenRegister from "../../StoreManager/StoreSeven/StoreSevenRegister/StoreSevenRegister";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import RequestGoodsStore from "../../StoreManager/RequestGoodsStore/RequestGoodsStore";
import ReturnGoodsStore from "../../StoreManager/ReturnGoodsStore/ReturnGoodsStore";
const DepartmentGoodsStore = ({ departmentId, stid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const [add, setAdd] = useState(false);
  const [returnStock, setReturnStock] = useState(false);

  const onBack = () => {
    navigate(`/q/${params.username}/member/department/${params.deptname}`, {
      state: getQuery.state,
    });
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onReturnToggle = () => {
    setReturnStock((pre) => !pre);
  };
  //   console.info("asfsdafgs", params);
  const onNavigate = () => {
    navigate(
      `/q/${params.username}/member/department/${params.deptname}?a=store&n=request`,
      {
        state: getQuery.state,
      }
    );
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>{t("store")}</h6>
            </div>
            <div className={style.fm_header_start_container}>
              <CreateButton
                label="request_stock"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
              <CreateButton
                label="return_stock"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onReturnToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <StoreOtherSevenTab
          inititalUrl={`/q/${params.username}/member/department/${params.deptname}?a=store`}
          parentState={getQuery.state}
        />
        <UserMemberContentWrapper>
          {searchParams.get("n") === "request" ? (
            <StoreSevenRequest flow="Department" mid={departmentId} />
          ) : searchParams.get("n") === "issue" ? (
            <StoreSevenIssue flow="Department" mid={departmentId} />
          ) : searchParams.get("n") === "stocktake" ? (
            <StoreSevenStocktake flow="Department" mid={departmentId} />
          ) : searchParams.get("n") === "consumption" ? (
            <StoreSevenConsumption flow="Department" mid={departmentId} />
          ) : searchParams.get("n") === "return" ? (
            <StoreSevenReturn flow="Department" mid={departmentId} />
          ) : searchParams.get("n") === "maintenance" ? (
            <StoreSevenMaintenance flow="Department" mid={departmentId} />
          ) : (
            <StoreSevenRegister flow="Department" mid={departmentId} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <RequestGoodsStore
          onNavigate={onNavigate}
          onClose={onToggle}
          mid={departmentId}
          stid={stid}
          flow="Department"
        />
      )}
      {returnStock && (
        <ReturnGoodsStore
          onNavigate={onNavigate}
          onClose={onReturnToggle}
          mid={departmentId}
          stid={stid}
          flow="Department"
        />
      )}
    </>
  );
};

export default DepartmentGoodsStore;
