import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import EmptyInfo from "../../../../../Loader/EmptyInfo";
import style from "../Exminations/Exminations.module.css";
import AddCoAttainmentSetting from "../../SubjectTeacher/SubjectCoAttainment/AddCoAttainmentSetting";
import {
  useGetAllCopoAttainment,
  useSubjectCoExcelUpload,
  useSubjectPoExcelUpload,
} from "../../../../../hooks/member_tab/subject-api";
import CoAttainmentSettingCard from "../../SubjectTeacher/SubjectCoAttainment/CoAttainmentSettingCard";
import UploadExcelButton from "../../../../../optimized/Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import QLoader from "../../../../../Loader/QLoader";

const DepartmentPo = ({ did }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [showingDataLoading, setShowingDataLoading] = useState(true);

  const [subjectPoExcelUpload] = useSubjectPoExcelUpload();
  const [subjectCoExcelUpload] = useSubjectCoExcelUpload();
  const [oneFileUpload] = useFileUpload();
  const { getAllCopoAttainment, getAllCopoAttainmentRefetch } =
    useGetAllCopoAttainment({
      data: {
        smid: did,
        flow: "VIEWING_COPO",
        filterBy: "DEPARTMENT",
      },
      skip: !did,
    });
  useEffect(() => {
    if (did) getAllCopoAttainmentRefetch();
  }, [did, getAllCopoAttainmentRefetch]);

  useEffect(() => {
    if (
      getAllCopoAttainment?.attainment?.length ||
      getAllCopoAttainment?.attainment?.length === 0
    ) {
      setShowingDataLoading(false);
    } else {
    }
  }, [getAllCopoAttainment?.attainment]);

  const onToggleAdd = () => {
    setAdd((pre) => !pre);
  };

  const onCoUpload = (e) => {
    if (e.target.files?.length > 0 && did) {
      setDisabled(true);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      oneFileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            subjectCoExcelUpload({
              did: did,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled(false);
              })
              .catch({});
          } else {
            setDisabled(false);
          }
        })
        .catch({});
    }
  };
  const onPoUpload = (e) => {
    if (e.target.files?.length > 0 && did) {
      setDisabled(true);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      oneFileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            subjectPoExcelUpload({
              did: did,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled(false);
              })
              .catch({});
          } else {
            setDisabled(false);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <div className={style.exam_container}>
        <div className={style.with_search}>
          <h6>{t("copo_attainment_department")}</h6>
          <section
            className={style.search_container}
            style={{
              gap: "1rem",
            }}
          >
            <UploadExcelButton
              label="co_excel"
              onExcelFileUpload={onCoUpload}
              dynamicKey="co_excel"
            />
            <UploadExcelButton
              label="po_excel"
              onExcelFileUpload={onPoUpload}
              dynamicKey="po_excel"
            />
          </section>
        </div>
        <BorderBottom
          customStyle={{
            marginTop: "0.5rem",
            marginBottom: "0.6rem",
            width: "100%",
          }}
        />

        <div className={style.add_new_container}>
          <p className={style.add_new_exam} onClick={onToggleAdd}>
            {t("create_new_po")}
          </p>
        </div>
        {getAllCopoAttainment?.attainment?.map((attainment) => (
          <CoAttainmentSettingCard
            attainment={attainment}
            key={attainment?._id}
            onRefetch={getAllCopoAttainmentRefetch}
          />
        ))}
        {!showingDataLoading && !getAllCopoAttainment?.attainment?.length && (
          <EmptyInfo
            customStyleContainer={{
              marginTop: "9rem",
            }}
            title={"No po found"}
            subtitle={"Please make sure you have created po."}
          />
        )}
        {add && (
          <AddCoAttainmentSetting
            onClose={onToggleAdd}
            smid={did}
            onRefetch={getAllCopoAttainmentRefetch}
            // count={getAllCopoAttainment?.attainment_count + 1}
            poCount={getAllCopoAttainment?.attainment_po_count + 1}
            isView="DEPARTMENT"
          />
        )}
        {showingDataLoading && <QvipleLoading />}
      </div>
      {disabled && <QLoader />}
    </>
  );
};

export default DepartmentPo;
